<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示店铺等级、升级条件及权益</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>名称</label>
        <el-input v-model="queryParams.levelName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
      <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/shopLevel/save'" icon="add" type="success" size="small" @click="$routerLink('/shopLevel/save','save')">新增</cms-button>
      <cms-button v-prem="'/shopLevel/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="levelName" label="名称" min-width='120'></el-table-column>
      <el-table-column  label="LOGO" min-width="150" prop="logo">
        <div slot-scope="scope" class="table-img">
          <img v-if="scope.row.resourcesSpaceData"   :src="$cms.filterUrl(scope.row.resourcesSpaceData.url) " alt="mockimg">
        </div>
        </el-table-column>
      <el-table-column label="排序" prop="sortNum" min-width="110">
        <template slot-scope="scope"  min-width="110">
          <el-input v-model="scope.row.sortNum" v-if="scope.row.levelType==1"
           @keyup.enter.native="sortNum($event,scope.row.id)" maxlength="2">
          </el-input>
          <div v-if="scope.row.levelType==2" style="padding-left:10px;">{{scope.row.sortNum}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="uploadMaxNum" label="图片空间数量上限" min-width="140"></el-table-column>
      <el-table-column prop="releaseMaxNum" label="商品发布数上限" min-width="140"></el-table-column>
      <el-table-column prop="exp" label="成长值" min-width="110"></el-table-column>
      <el-table-column label="操作" width='280' fixed="right" align="right">
        <div slot-scope="scope">
          <cms-button icon='edit' v-prem="'/shopLevel/prem'" size='small' type='info' 
           @click.stop="$routerLink('/shopLevel/prem','update',{id:scope.row.id,name:scope.row.levelName})" >分配权限</cms-button>
          <cms-button icon='edit' v-prem="'/shopLevel/edit'" size='small' type='info' v-if="scope.row.levelType!=2"
           @click.stop="$routerLink('/shopLevel/edit','update',{id:scope.row.id})">编辑
          </cms-button>
          <cms-button icon='error' v-prem="'/shopLevel/delete'" size='small' type='info'  v-if="scope.row.levelType!=2"
           @click.stop="deleteData(scope.row.id)">删除
          </cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  gradeList,
  gradeDalete,
  gradeSort
} from '@/api/shop/shopLevel.js'
var defaultData = {
  storeName: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(gradeDalete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      let flag = true
      this.tableData.forEach(v => {
        this.ids.forEach(item => {
          if (v.id === item) { // 匹配到相同的项
            if (v.levelType === 2) { // 判断等级类型
              flag = false
            }
          }
        })
      })
      if (flag) {
        this.deleteTableDatas(gradeDalete, this.ids) // 这个才是真实的删除方法
      } else {
        this.$errorMessage('自营店铺等级不可删除')
      }
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          gradeSort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(gradeList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(gradeList, this.queryParams)
  }
}
</script>

<style>
</style>



